
  import { defineComponent } from 'vue';
  import { mapGetters } from 'vuex';

  export default defineComponent({
    name: 'PagesHistory',
    computed: {
      ...mapGetters([
        'activePost'
      ]),
      activeLink(){
        return this.$router.currentRoute.value;
      },
      blogPage(){
        const pageName = this.$router.currentRoute.value?.name;
        return (pageName === 'post');
      }
    }
  });
